/* Pixel Fantasy Prism Theme */
pre[class*="language-"],
code[class*="language-"] {
  color: #f8f8f2;
  background: #1a1a2e; /* deep night sky */
  font-family: "Press Start 2P", "VT323", monospace; /* pixel fantasy font */
  font-size: 14px;
  line-height: 1.6;
  border: 3px solid #6a0dad; /* magical purple border */
  border-radius: 6px;
  box-shadow: 0 0 12px #ff00ff; /* glowing neon aura */
  padding: 1em;
  overflow-x: auto;
}

/* Keywords = golden runes */
.token.keyword {
  color: #ffcc00;
  font-weight: bold;
}

/* Strings = aqua glow */
.token.string {
  color: #00ffcc;
}

/* Functions = enchanted violet */
.token.function {
  color: #c586c0;
}

/* Comments = ghostly gray whispers */
.token.comment {
  color: #888;
  font-style: italic;
}

/* Numbers = fiery red sparks */
.token.number {
  color: #ff5555;
}

/* Operators = silver steel */
.token.operator {
  color: #cccccc;
}

/* Variables = emerald green */
.token.variable {
  color: #50fa7b;
}

/* Line numbers plugin styling */
pre.line-numbers {
  position: relative;
  padding-left: 3.5em;
}

.line-numbers .line-numbers-rows {
  border-right: 2px solid #6a0dad;
  background: #0f0f1a;
}

.line-numbers-rows > span {
  color: #666;
  text-shadow: 0 0 5px #6a0dad;
}
