html,
body {
  cursor: url('../../images/skeleton-cursor.png') 8 0, auto;
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #1a0f00;
  background-image: url('../../images/Background.png');
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100% 100%;
  image-rendering: pixelated;
  overflow: hidden;
}

.MainContainer {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: 'Press Start 2P', monospace;
  color: #f3e9d2;
  text-align: center;
  image-rendering: pixelated;
}

.Title {
  width: 120vw;
  height: 120vh;
  background-image: url('../../images/Title.png');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: bottom center;
  image-rendering: pixelated;
}

.Altart {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  bottom: 10vh;
  width: 100%;
  height: auto;
  pointer-events: none;
}

.Altart .Candle {
  width: 10vw;
  height: 20vh;
  background-image: url('../../images/Candle.png');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: bottom center;
}

.Altart .book-img {
  width: 25vw;
  height: 25vh;
  pointer-events: auto;
}

.Table {
  width: 120vw;
  height: 120vh;
  background-image: url('../../images/Table.png');
  background-size: auto;
  background-repeat: no-repeat;
  background-position: bottom center;
  image-rendering: pixelated;
}

/* Book image styling */
.book-img,
.book-img-hidden {
  width: 25vw;
  height: auto;
  image-rendering: pixelated;
  filter: drop-shadow(2px 2px 0 #000) drop-shadow(-2px -2px 0 #000) brightness(1);
  transition: filter 0.3s ease;
}

.book-img-hidden {
  visibility: hidden;
}

.book-img:hover {
  filter: drop-shadow(2px 2px 0 #000) drop-shadow(-2px -2px 0 #000) brightness(1.2);
}

.modal-overlay {
  width: 120vw;
  height: 120vh;
  background-image: url('../../images/GrimoireOpenedBook.png');
  background-repeat: no-repeat;
  background-position: center;
  background-blend-mode: darken;
  background-size: 65%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  animation: riseFromBook 0.6s ease-out;
  overflow-y: auto;
}

.modal-content {
  background: rgba(0, 0, 0, 0.6);
  width: 75%;
  height: 100vh;
  text-align: left;
  color: #f3e9d2;
  font-family: 'Press Start 2P', monospace;
  display: flex;
  flex-direction: row;
}

.modal-menu {
  background: rgba(0, 0, 0, 0.8);
  font-family: 'Press Start 2P', monospace;
  image-rendering: pixelated;
  transition: width 0.3s ease;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  width: 25%;
  font-size: clamp(1rem, 2vw, 1.5rem);
}

.modal-menu button,
.modal-menu li button {
  font-size: inherit;
  text-align: center;
}

.menu-toggle {
  background: none;
  border: none;
  color: #f3e9d2;
  font-size: 1.25rem;
  cursor: url('../../images/skeleton-cursor.png') 8 0, auto;
  margin-bottom: 1rem;
}

.modal-menu ul {
  list-style: none;
  font-size: medium;
  padding: 0;
}

.modal-menu li {
  margin-bottom: 1rem;
}

.modal-menu li button {

  background: none;
  border: none;
  border-bottom: 2px solid currentColor;
  color: inherit;
  font-family: inherit;
  font-size: large;
  cursor: url('../../images/skeleton-cursor.png') 8 0, auto;
  text-align: center;
  width: 100%;
}

.modal-menu ul li {
  background: none;
  border: none;
  color: inherit;
  font-family: inherit;
  font-size: medium;
  cursor: url('../../images/skeleton-cursor.png') 8 0, auto;
  text-align: center;
  width: 100%;
}

.modal-main {
  flex-grow: 1;
  padding: 40px;
  background: rgba(0, 0, 0, 0.6);
  color: #f3e9d2;
}

.post-section {
  padding: 1rem;
  flex: 1;
  overflow-y: auto;
}

.post-content article {
  margin-bottom: 2rem;
  border-bottom: 1px solid #ccc;
}


/* Responsive adjustments */
@media (max-width: 1024px) {
  .MainContainer {
    width: 80vw;
  }

  .Altart {
    flex-direction: column;
    align-items: center;
  }

  .Altart .Candle {
    display: none;
  }

  .Altart .book-img {
    width: 90vw;
    height: auto;
  }

  .Altart .book-img:hover {
    filter: drop-shadow(2px 2px 0 #000) drop-shadow(-2px -2px 0 #000) brightness(1.2);
  }

  .Table {
    background: none !important;
    justify-content: start;
    align-items: start;
  }
}

@media (max-width: 768px) {
  .MainContainer {
    width: 90vw;
  }

  .Altart {
    flex-direction: column;
    align-items: center;
  }

  .Altart .Candle {
    display: none;
  }

  .Altart .book-img {
    width: 50vw;
    height: auto;
  }

  .Altart .book-img:hover {
    filter: drop-shadow(2px 2px 0 #000) drop-shadow(-2px -2px 0 #000) brightness(1.2);
  }

  .Table {
    background: none !important;
    align-items: start;
    justify-content: start;
  }
}

@media (max-width: 480px) {
  .MainContainer {
    width: 95vw;
  }

  .Altart {
    flex-direction: column;
    align-items: center;
  }

  .Altart .Candle {
    display: none;
  }

  .Altart .book-img {
    width: 70vw;
    height: auto;
  }

  .Altart .book-img:hover {
    filter: drop-shadow(2px 2px 0 #000) drop-shadow(-2px -2px 0 #000) brightness(1.2);
  }

  .Table {
    background: none !important;
    align-items: start;
    justify-content: start;
  }
}